<template>
  <div>
    <div class="container">
      <div class="d-flex justify-content-between mb-2">
        <h2></h2>
        <button class="btn btn-primary" type="button" @click="editData()"></button>
      </div>
      <table class="table">
        <tr>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
        </tr>
        <tr v-for="(couponData, index) in couponDatas" :key="`coupon${index}`">
          <td>{{ couponData.code }}</td>
          <td>{{ couponData.title }}</td>
          <td>{{ couponData.percent }}</td>
          <td>
            <template v-if="couponData.enabled">
              <span class="text-success"></span>
            </template>
            <template v-else>
              <span></span>
            </template>
          </td>
          <td>
            {{ couponData.deadline.datetime }}<br />
            ({{ couponData.deadline.diff }})
          </td>
          <td>
            <button class="btn btn-primary mr-2" type="button" @click="editData(couponData)">
              
            </button>
            <button
              class="btn btn-danger"
              type="button"
              @click="deleteQA(couponData.id, couponData.title)"
            >
              
            </button>
          </td>
        </tr>
      </table>

      <!-- pagination -->
      <Pagination :pages="pagination" @emit-pages="getData" name="pagination"></Pagination>

      <!-- deleteModal -->
      <div
        class="modal fade bd-example-modal-sm"
        id="deleteModal"
        tabindex="-1"
        role="dialog"
        aria-labelledby="exampleModalCenterTitle"
        aria-hidden="true"
      >
        <div class="modal-dialog modal-sm modal-dialog-centered" role="document">
          <div class="modal-content">
            <div class="modal-header">
              <h5 class="modal-title" id="exampleModalLongTitle"></h5>
              <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                <span aria-hidden="true">&times;</span>
              </button>
            </div>
            <div class="modal-body"> {{ tempDelete.title }}?</div>
            <div class="modal-footer">
              <button type="button" class="btn btn-secondary" data-dismiss="modal"></button>

              <button type="button" class="btn btn-danger" @click="deleteData()"></button>
            </div>
          </div>
        </div>
      </div>

      <!-- Modal -->
      <div
        class="modal fade bd-example-modal-lg"
        id="editModal"
        tabindex="-1"
        role="dialog"
        aria-labelledby="exampleModalCenterTitle"
        aria-hidden="true"
      >
        <div class="modal-dialog modal-lg modal-dialog-centered" role="document">
          <div class="modal-content">
            <div class="modal-header">
              <h5 class="modal-title" id="exampleModalLongTitle">
                <template v-if="tempData.id">
                  
                </template>
                <template v-else>
                  
                </template>
              </h5>
              <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                <span aria-hidden="true">&times;</span>
              </button>
            </div>
            <div class="modal-body">
              <form class="form-group">
                <div class="row">
                  <div class="col-sm-12">
                    <table>
                      <tr>
                        <th width="30%">
                          
                        </th>
                        <td width="70%">
                          <input
                            type="text"
                            class="form-control"
                            v-model="tempData.title"
                            placeholder=""
                            required
                          />
                        </td>
                      </tr>
                      <tr>
                        <th></th>
                        <td>
                          <input
                            type="text"
                            class="form-control"
                            v-model.number="tempData.code"
                            placeholder=""
                          />
                        </td>
                      </tr>
                      <tr>
                        <th></th>
                        <td>
                          <input
                            type="text"
                            class="form-control"
                            v-model.number="tempData.percent"
                            placeholder=""
                          />
                        </td>
                      </tr>
                      <tr>
                        <th></th>
                        <td>
                          <div class="custom-control custom-switch">
                            <input
                              type="checkbox"
                              class="custom-control-input"
                              id="customSwitches"
                              v-model="tempData.enabled"
                            />
                            <label class="custom-control-label" for="customSwitches"></label>
                          </div>
                        </td>
                      </tr>
                      <tr>
                        <th></th>
                        <td>
                          <div class="custom-control custom-switch">
                            <input
                              type="text"
                              class="form-control"
                              v-model="dueDate"
                              placeholder="Y-m-d"
                            />

                            <input
                              type="text"
                              class="form-control"
                              v-model="dueTime"
                              placeholder="H:i:s"
                            />
                          </div>
                        </td>
                      </tr>
                    </table>
                  </div>
                </div>
              </form>
            </div>
            <div class="modal-footer">
              <button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>

              <button type="button" class="btn btn-primary" @click="updateData()"></button>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="mt-5"></div>
  </div>
</template>
<script>
/* global $*/
import Pagination from "@/components/_shared/admin/Pagination";
import moment from "moment";

const getCouponUrl = `${process.env.VUE_APP_PATH}${process.env.VUE_APP_UUID}/admin/ec/coupons`;
const editCouponUrl = `${process.env.VUE_APP_PATH}${process.env.VUE_APP_UUID}/admin/ec/coupon`;
export default {
  components: {
    Pagination
  },
  data() {
    return {
      coupontListSettings: {
        page: 1,
        paged: 10
      },
      pagination: {
        current_page: 1
      },
      tempDelete: {
        id: "",
        title: ""
      },
      couponDatas: [],
      baseData: {
        deadline: [],
        title: "",
        enabled: false,
        percent: 100,
        deadline_at: 0,
        code: ""
      },
      dueDate: "",
      dueTime: "",
      tempData: {
        title: "",
        enabled: false,
        percent: 100,
        deadline_at: 0,
        code: ""
      }
    };
  },
  created() {
    this.token = document.cookie.replace(/(?:(?:^|.*;\s*)token\s*=\s*([^;]*).*$)|^.*$/, "$1");
    this.$http.defaults.headers["Authorization"] = `Bearer ${this.token}`;
    this.getData();
  },
  methods: {
    //pagenation
    emitPages(val) {
      console.log("nav", val);
    },

    // api
    getData(page = 1) {
      this.$parent.isLoading = true;
      const api = `${getCouponUrl}`;
      this.coupontListSettings.page = page;
      this.$http
        .get(api)
        .then(response => {
          console.log(response);
          this.couponDatas = response.data.data;
          this.$parent.isLoading = false;
          this.pagination = response.data.meta.pagination;
        })
        .catch(err => {
          console.log("err ", err);
        });
    },
    //+
    updateData() {
      //  Y-m-d H:i:s2020-06-16 09:31:18
      this.tempData.deadline_at = `${this.dueDate} ${this.dueTime}`;

      //update data
      if (this.tempData.id) {
        this.apiEditData(this.tempData.id);
        this.couponDatas.forEach((couponData, idx) => {
          if (this.tempData.id === couponData.id) {
            this.couponDatas[idx] = this.tempData;
          }
        });
      } else {
        //new data
        const id = new Date().getTime(); // Unix Timestamp
        this.tempData.id = id;
        this.couponDatas.push(this.tempData);
        this.apiAddNewData(this.tempData);
      }
      //
      this.tempData = JSON.parse(JSON.stringify(this.baseData));
      $("#editModal").modal("hide");
    },

    //
    editData(data = this.baseData) {
      this.tempData = JSON.parse(JSON.stringify(data));

      // 
      if (this.tempData.deadline.length) {
        const dueDateTime = this.tempData.deadline.datetime.split(" ");
        [this.dueDate, this.dueTime] = dueDateTime;
      } else {
        this.dueDate = moment().format("yyyy-MM-DD");
        this.dueTime = moment().format("hh:mm:ss");
      }
      $("#editModal").modal("show");
    },
    //api
    apiEditData(id) {
      const url = `${editCouponUrl}/${id}`;
      this.$parent.isLoading = true;
      this.$http
        .patch(url, this.tempData)
        .then(response => {
          console.log(response);
          this.getData();
        })
        .catch(err => {
          console.log(err);
        });
    },
    //api
    apiAddNewData(data) {
      this.$parent.isLoading = true;
      this.$http
        .post(editCouponUrl, data)
        .then(() => {
          this.getData();
        })
        .catch(err => {
          console.log(err);
        });
    },

    deleteQA(id, title) {
      this.tempDelete.id = id;
      this.tempDelete.title = title;
      $("#deleteModal").modal("show");
    },
    deleteData() {
      const id = this.tempDelete.id;
      this.apiDeleteData(id);
      //update screen
      this.couponDatas.forEach((couponData, idx) => {
        if (couponData.id === id) {
          this.couponDatas.splice(idx, 1);
        }
      });
      this.tempDelete.id = "";
      this.tempDelete.title = "";
      $("#deleteModal").modal("hide");
    },
    apiDeleteData(id) {
      this.$parent.isLoading = true;
      //api
      const url = `${editCouponUrl}/${id}`;
      this.$http
        .delete(url)
        .then(() => {
          this.getData();
        })
        .catch(err => {
          console.log(err);
        });
    }
  }
};
</script>
